.create-new-task{
    display: flex;
    width: 100%;
    justify-content: center;
}
.create-new-task button {
    --bg: #e66100;
    --text-color: #fff;
    position: relative;
    width: 200px;
    border: none;
    background: var(--bg);
    color: var(--text-color);
    padding: 1em;
    font-weight: bold;
    text-transform: uppercase;
    transition: 0.2s;
    border-radius: 5px;
    opacity: 0.8;
    letter-spacing: 1px;
    box-shadow: #c0392b 0px 7px 2px, #000 0px 8px 5px;
    margin-bottom: 20px;
  }
  
  button:hover {
    opacity: 1;
    cursor: pointer;
  }
  
  button:active {
    top: 4px;
    box-shadow: #c0392b 0px 3px 2px,#000 0px 3px 5px;
  }